/* Стили компонента Searchbar */
.searchbar {
  top: 20px;
  left: 0;
  position: sticky;
  z-index: 1100;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 64px;
  padding-right: 24px;
  padding-left: 24px;
  padding-top: 12px;
  padding-bottom: 12px;
  color: #fff;
  background-color: #009a63;
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
  border-radius: 80px/50px;
  width: 60%;
}

/*
 * Стили компонента SearchForm
 */
.form {
  display: flex;
  align-items: center;
  width: 100%;
  max-width: 600px;
  background-color: #9ee488;
  border-radius: 7px;
  overflow: hidden;
  border-radius: 40px/16px;
}

.input {
  display: inline-block;
  width: 80%;
  font: inherit;
  font-size: 20px;
  border: none;
  outline: none;
  padding-left: 4px;
  padding-right: 4px;
}

.input::placeholder {
  font: inherit;
  font-size: 16px;
}

.button {
  /* display: inline-block; */
  width: 60px;
  height: 24px;
  border: 0;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  outline: none;
  background-color: rgb(73, 194, 136);
  font-size: 18px;
}

.button:hover {
  background-color: #9bcd71;
}
